<template>
	<div>
		<common-head :view="{
			goback: true,
			pageTitle: '个人资料'
		}"></common-head>

		<ul class="setting_list">
			<li class="head_img">
				<label>头像</label>				
				<i class="iconfont icon-right"></i>
				<span>
					<img v-if="!headUrl" src="../assets/headUrl.jpg">
            		<img v-else :src="headUrl">
					<input type="file" accept="image/*" @change="upload">
				</span>
			</li>
			<li>
				<label>手机</label>
				<span>{{telephone}}</span>
			</li>
			<li>
				<router-link to="/modifyPassword">
					<label>修改密码</label>				
					<i class="iconfont icon-right"></i>
					<span>********</span>
				</router-link>
			</li>
		</ul>
	</div>
</template>

<script>
import commonHead from '../components/commonHead'
import {msgTip} from '../plugins/util'
import {getUserInfo, postHeaderImg, ajax} from '../data/service'

export default{
	data(){
		return{
			headUrl: '',
			telephone: '',
			headerImg: '',
		}
	},
	created(){
		this.getUserBase()
	},
	mixins:[ajax],
	methods:{
		async getUserBase(){
			this.ajax(await getUserInfo(), function(resp){
				this.headUrl = resp.headUrl
				this.telephone = resp.phoneBind
			})
		},
		upload(e){			
			var that = this
			if(e.target.files && e.target.files[0]) {
				let file = e.target.files[0]
		        let reader = new FileReader()
		        let img = new Image() 
		        reader.onload = function (e) {
		            if(e.target.result.length > (100 * 1024)){//图片大于100kb则压缩
		                img.src = e.target.result;
		                img.onload=function(){
		                    let data = that.compress(img)
		                    that.headerImg = data
		                    that.postImg()
		                }
		            }else{
		                that.headerImg = e.target.result
		                that.postImg()
		            }
		        }
		        // 将图片2将转成 base64 格式  
		        reader.readAsDataURL(file);
		    }
		},
		async postImg(){	
			this.ajax(await postHeaderImg(this.headerImg), function(resp){
				this.headUrl = resp.headerImgSrc
				msgTip('修改成功！')
			})
		},
		compress(img){			
			let initSize = img.src.length;
		    let width = img.width;
		    let height = img.height;
		    let canvas = document.createElement("canvas");
		    let ctx = canvas.getContext('2d');
		    
		    //如果图片大于四百万像素，计算压缩比并将大小压至400万以下
		    let ratio;
		    if ((ratio = width * height / 4000000)>1) {
		        ratio = Math.sqrt(ratio);
		        width = width/ratio;
		        height = height/ratio;
		    }else {
		        ratio = 1;
		    }
		    canvas.width = width;
		    canvas.height = height;
		    //铺底色
		    ctx.fillStyle = "#fff";
		    ctx.fillRect(0, 0, canvas.width, canvas.height);
		    ctx.drawImage(img, 0, 0, width, height);
		    //进行最小压缩
		    let ndata = canvas.toDataURL("image/jpeg", 0.1);
		    console.log('压缩前：' + initSize)
		    console.log('压缩后：' + ndata.length)
		    canvas.width = canvas.height = 0;
		    return ndata;
		}
	},
	components:{
		commonHead
	}
}
</script>

<style scoped>
.setting_list{padding-top:.48rem;}
.setting_list li{margin-top:.1rem; background:#fff; font-size:.16rem; color:#666; padding:.12rem .16rem; overflow:hidden;}
.setting_list li label{width:;}
.setting_list li span{float:right; margin-right:.12rem; margin-top:.03rem;}
.setting_list li i{float:right; color:#cdced2; margin-top:.04rem;}
.setting_list li a{color:#666;}
.setting_list li.head_img label{display:inline-block; padding-top:.3rem;}
.setting_list li.head_img span{position:relative; margin-top:0; display:inline-block; width:.78rem; height:.78rem; overflow:hidden;}
.setting_list li.head_img span input{width:.78rem; height:.78rem; background:#fff; position:absolute; top:0; left:0; opacity:0;}
.setting_list li.head_img img{width:.78rem; height:.78rem; border-radius:50% 50%;}
.setting_list li.head_img i{margin-top:.32rem;}
</style>


